	module.exports = {"data":{"markdownRemark":{"html":"<p>2012年12月29日、東京ビッグサイトにて開催される即売会「コミックマーケット83」にサークル参加いたします。 ブースの場所は<strong>1日目(土曜日) 東Ｒ-16b “danmaq”</strong>です。 (いつも東方カテゴリで2日目に参加しているけど、今回は<strong>1日目の12月29日です！</strong>くれぐれも間違えないように！) 当日頒布予定のアイテムは以下の通りです。</p>\n<ul>\n<li><strong>[NEW!]</strong> <a href=\"/?p=1103\">EVIL MAGIC ～ 邪の魔術 ¥800</a></li>\n<li><a href=\"/!/thC/\">東方夢終劇 ～ Concealed the Conclusion 5周年記念デラックスパック</a> ¥1,000</li>\n<li><a href=\"/!/leila/\">Leila Prismriver</a> ¥300</li>\n<li><a href=\"/!/dst/\">danmaq soundtrack</a> ¥100</li>\n<li><a href=\"http://kagaminer.in/\">結界幻想録 鏡 ～ mythical mirror</a> ¥1,000</li>\n<li><a href=\"http://kagaminer.in/\">結界幻想録 鏡 サウンドトラック</a> ¥500</li>\n<li><strong>[NEW?]</strong> おまけ 2013年賀状代わりのポストカード ¥0</li>\n</ul>\n<h2>各タイトルの紹介</h2>\n<p><img src=\"https://danmaq.com/wp-content/uploads/2012/11/em-229x300.jpg\" alt=\"EVIL MAGIC ～ 邪の魔術\"></p>\n<p><a href=\"\">オリジナル</a>[新刊]<strong><a href=\"/?p=1103\">EVIL MAGIC ～ 邪の魔術</a></strong></p>\n<blockquote>\n<p>ちょっと不思議な能力が使える女の子エナと、その弟のグレース、そして二人の幼馴染であるアーク、10代前半のまだまだお子様な悪ガキ三人衆の、ドタバタ大暴れストーリー！ところが、この地の領主はもっと悪いやつで、ある日アークを攫ってしまった！幼馴染の彼を取り返すため、村を飛び出した姉弟。そしてエナと同じ能力者である、領主と決闘することに。果たして二人はアークを取り返すことができるのか！？そして謎の能力『邪の魔術』とは？全180ページの本格ファンタジー小説！</p>\n</blockquote>\n<p><img src=\"https://danmaq.com/wp-content/uploads/2012/11/em_POP-300x300.png\" alt=\"まだまだお子様な、悪ガキ三人衆が大暴れ！剣と魔法の、本格ファンタジー小説！\"> 全180ページのオフセ本ライトノベルです(本文はオンデマンドです)。カバーイラスト、および挿絵は<a href=\"http://dungeonmania.web.fc2.com/\">桜姫つかさ</a>さんに描いていただきました！(ちなみに一箇所だけ私が描いたイラストが混じっていますので、よければ探してみてください) (どうでもいい話ですが)<strong>東方夢終劇のシナリオが好きな方には、割と楽しめる内容だと思います！</strong>勿論知らない人も今回東方じゃなくてオリジナルものですので十分に楽しめます！ 今回都合により<em>少部数のみの取り扱い</em>とさせていただきます。万一完売しました際、または一日目は都合が悪くて行けないよ！って方は<strong><a href=\"http://www.toranoana.jp/mailorder/article/04/0030/08/49/040030084948.html\">とらのあな</a>や<a href=\"http://shop.melonbooks.co.jp/shop/detail/212001056182\">メロンブックス</a>でも委託取扱</strong>いたしますので、良ければそちらもご利用ください。各委託サイトにてサンプル画像も掲載しています。 <img src=\"https://danmaq.com/wp-content/uploads/2012/11/thM_outside-300x296.jpg\" alt=\"結界幻想録 ～ mythical mirror\"> <a href=\"\">東方</a>[2012/8リリース]<strong><a href=\"http://kagaminer.in/\">結界幻想録 鏡 ～ mythical mirror</a></strong></p>\n<blockquote>\n<p>此処のようで此処ではなく、でもやっぱり此処のような幻想郷。 彼女たちはそんな場所へと突如、迷い込んでしまう。 元の場所へ帰る方法を探すため、こんなことを起した元凶を探すため、言いようのない違和感の正体を明かすため、取り合えずそれっぽさそうなところへと飛んでいくことにした── 東方二次創作となる、幻想弾幕シューティングゲームです。</p>\n</blockquote>\n<p>こちらは<a href=\"http://dangoya.moo.jp/\">DANGOYA</a>さんとの合作です。併せてサウンドトラックの頒布も500円で行う予定です。 <img src=\"https://danmaq.com/wp-content/uploads/2012/11/c70-212x300.jpg\" alt=\"東方夢終劇\"> <a href=\"\">東方</a>[2011/12リリース]<strong><a href=\"/!/thC/\">東方夢終劇 ～ Concealed the Conclusion 5周年記念デラックスパック</a></strong></p>\n<blockquote>\n<p>2005年8月リリースの「<a href=\"/!/thA/\">東方時封城 ～ the Alternative Age</a>」と2006年12月リリースの「東方夢終劇 ～ Concealed the Conclusion」を1枚のDVDにまとめて再販いたします！どちらも東方弾幕風ベースの東方二次創作縦型シューティングゲームです。過激な弾幕や、本家とは趣向の異なる鬱傾向なストーリーがお好みな方にお勧めです！さらに本DVDには追加特典として、2006年8月に出す予定でお蔵入りした時封城v2のサウンドトラックをWAVサウンド形式で収録、また夢終劇を弾幕風から.NETに移植した「東方夢終劇v3」のBETA版を収録しています。</p>\n</blockquote>\n<ul>\n<li>\n<p><a href=\"/!/thA/story.html\">東方時封城 ～ the Alternative Ageのあらすじ</a>(長いので別リンク)</p>\n</li>\n<li>\n<p><a href=\"/!/thC/story.html\">東方夢終劇 ～ Concealed the Conclusionのあらすじ</a>(長いので別リンク)</p>\n</li>\n<li>\n<p><a href=\"http://www.toranoana.jp/mailorder/article/04/0030/06/41/040030064180.html\">一日目は来れないよ！って方のために、<strong>とらのあなさんにて委託再頒布</strong>(¥1,498/税込)を行っています。 (<strong>プレイ動画も用意</strong>していますので当日来られる方もチェックしてみてください！)</a></p>\n</li>\n<li>\n<p><a href=\"https://www1n.sppd.ne.jp/danmaq.com/e-danmaq/?type=cat&#x26;no=00001000005\">わいは巨人ファンやさかい、とら通販なんぞ使えるかいダボ！って方はこちらでも購入できます！</a>\n<img src=\"https://danmaq.com/wp-content/uploads/2012/11/cap_s.jpg\" alt=\"オビのサンプル画像\"> なお、今回も引き続き<strong>「東方夢終劇 5周年記念デラックスパック」のアップデート割</strong>を実施いたします。東方時封城(プレス版)または東方夢終劇v1～v2.01(CD-ROM版)のオビをお持ちの方は、持参していただきますと、アップデート割として以下の価格で頒布させていただきます！</p>\n</li>\n<li>\n<p>時封城のオビをお持ちの方：¥1,000→¥800</p>\n</li>\n<li>\n<p>時封城(CD-R版)をお持ちの方：アプデ割対象外</p>\n</li>\n<li>\n<p>夢終劇(CD-ROM版)のオビをお持ちの方：¥1,000→¥200</p>\n</li>\n</ul>","frontmatter":{"cover":null,"date":"2012-12-26T19:50:41+09:00","strDate":"2012/12/26","title":"コミックマーケット83参加情報"}}},"pathContext":{"path":"/ja/blog/2012/12/26-c83/","langKey":"ja"}}

